/* src/App.css */

.App {
    text-align: center;
  }
  
  .app {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Arial', sans-serif;
  }
  
  .controls {
    margin-bottom: 25px;
  }
  
  .movie-list {
    width: 300px;
  }
  
  .movie-list ul {
    list-style-type: none;
    padding: 0;
  }
  
  .movie-list li {
    cursor: pointer;
    padding: 8px;
    background-color: #e7a90c;
    margin-bottom: 5px;
    border-radius: 5px;
  }
  
  .movie-details {
    margin-left: 20px;
  }
  
  .content {
    display: flex;
  }
  
  .left-pane {
    width: 300px;
  }
  
  .right-pane {
    flex-grow: 1;
    margin-left: 20px;
  }
  
  .app-title {
    font-size: 30px;
    margin-bottom: 30px;
    background-color: #e0cc17;
  }
  